
@import url('https://fonts.googleapis.com/css2?family=Hind+Guntur&family=Roboto:ital,wght@0,300;1,300&display=swap');

body {
    margin: 0px;
}

body > #root > div {
    height: 100vh;
}

.full-height {
    height: 100%;
    background: no-repeat center center; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

div {
    font-family: 'Hind Guntur', sans-serif;
    font-size: 2rem; 
}

.row {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
}
